/** @jsx React.DOM */

var React = require('react'),
	Router = require('react-router'),
	Link = Router.Link,
	Col = require('react-bootstrap/Col'),
	Input = require('react-bootstrap/Input'),
	Icon = require('../helper/icon.jsx'),
	LocSelect = require('./locSelect.jsx');

module.exports = HederLogo = React.createClass({displayName: "HederLogo",
	render: function () {
		var searchAddon = (
			React.createElement("button", {type: "submit"}, 
				React.createElement(Icon, {name: "search-icon"})
			)
		);

		return (
			React.createElement(Col, {className: "header-logo", xs: 12, md: 7}, 
				React.createElement(Col, {className: "logo", xs: 3, md: 3}, 
					React.createElement(Link, {to: "app"}, 
						React.createElement("img", {src: "img/logo.png"})
					)
				), 
				React.createElement(Col, {className: "header-search", xs: 9, md: 9}, 
					React.createElement(LocSelect, null), 
					React.createElement("div", {id: "imaginary_container"}, 
						React.createElement(Input, {
							type: "text", 
							addonAfter: searchAddon}
						)
					)
				)
			)
		);
	}
});