/** @jsx React.DOM */

var React = require('react'),
	ReactBootstrap = require('react-bootstrap'),
	DropdownButton = ReactBootstrap.DropdownButton,
	ButtonToolbar = ReactBootstrap.ButtonToolbar,
	MenuItem = ReactBootstrap.MenuItem;

var LocSelect = React.createClass({displayName: "LocSelect",
	getDefaultProps: function () {
		return {
			countries: {
				"dub": "Dubai",
				"aac": "Aachen",
				"ber": "Berlin"
			},
			default: "dub"
		}
	},

	render: function () {
		var title = this.props.countries[this.props.default];

		var items = [];

		for (var country in this.props.countries) {
			if (this.props.countries.hasOwnProperty(country)) {
				items.push(React.createElement(MenuItem, {key: country, eventKey: country}, this.props.countries[country]));
			}
		}
		// selectpicker bla bla bli
		return (
			React.createElement("div", {className: "country select-style"}, 
				React.createElement(ButtonToolbar, null, 
					React.createElement(DropdownButton, {onClick: this.onClick, key: "countrySelect", title: title, id: "id_select", className: "selectpicker bla bli"}, 
						items
					)
				)
			)
		);
	},

	onClick: function(e) {
		console.log("yolo");
	}
});

module.exports = LocSelect;